.Site {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  background-color: #000014;
  color: #1927e3;
}

.Link {
  color: #262934;
  font-weight: 800;
  text-decoration: none;

  &:hover {
    color: #f8ec34;
    font-style: italic;

    &::after {
      content: "!";
    }
  }
}

.Icon {
  position: absolute;
  top: 100px;

  .Cowboy {
    width: 50px;
    height: auto;

    path {
      fill: #262934;
    }

    &:hover {
      path {
        fill: #f8ec34;
      }
    }
  }
}

p {
  margin: 0;
  font-size: 16px;
}
